import React, { useState } from 'react'

import styles from './table.module.css'
import TableHeaderComponent from './table-header/table-header.component'
import TableBodyComponent from './table-body/table-body.component'
import TableFooterComponent from './table-footer/table-footer.component'
import DataViewComponent from '../data-view/data-view.component'

function TableComponent(props) {
  const [dataView, setDataView] = useState({})
  const [activeDataView, setActiveDataView] = useState(false)

  const displayDataView = (data) => {
    setDataView(data)
    setActiveDataView(true)
  }

  const closeDataView = () => {
    setDataView({})
    setActiveDataView(false)
  }

  return (
    <div className="d-flex">
      <div className={styles.table}>
        <div className={styles.tableDiv}>
          <table>
            <TableHeaderComponent activeDataView={activeDataView} />
            <TableBodyComponent
              data={props.data}
              activeDataView={activeDataView}
              displayDataView={displayDataView}
            />
          </table>
        </div>
        <TableFooterComponent
          getData={props.getData}
          decrementPageButtonNumbers={props.decrementPageButtonNumbers}
          paginateTo={props.paginateTo}
          incrementPageButtonNumbers={props.incrementPageButtonNumbers}
          currentPage={props.currentPage}
          pageSize={props.pageSize}
          totalData={props.totalData}
          pageButtons={props.pageButtons}
          activeDataView={activeDataView}
        />
      </div>
      <div
        style={{ display: activeDataView ? null : 'none', paddingLeft: '10px' }}
      >
        <div className={styles.dataViewPanel}>
          <DataViewComponent closeDataView={closeDataView} data={dataView} />
        </div>
      </div>
    </div>
  )
}

export default TableComponent
